<template>
    <div>
      <header>
        <h1>FromU2Me</h1>
      </header>
  
      <section>
        <div id="login">
          <form @submit.prevent="submitForm" >
            <div class="imgcontainer">
              <img src="/Images/smile.jpg" alt="Avatar" class="avatar" />
              <h1>Registar Equipamento</h1>
            </div>
  
            <div class="container">
              <label for="marca">Marca</label>
              <select required id="marca" name="marca" v-model="form.marca">
                <option value="Xiaomi">Xiaomi</option>
                <option value="Nokia">Nokia</option>
                <option value="Outra">Outra</option>
              </select>
  
              <label for="modelo"><b>Modelo</b></label>
              <input
                type="text"
                placeholder="Introduza um Modelo"
                name="modelo"
                v-model="form.modelo"
                required
              />
  
              <label for="tipo"><b>Tipo</b></label>
              <input
                type="text"
                placeholder="Introduza um tipo"
                name="tipo"
                v-model="form.tipo"
              />
  
              <label for="categoria"><b>Categoria</b></label>
              <input
                type="text"
                placeholder="Introduza uma Categoria"
                name="categoria"
                v-model="form.categoria"
              />
  
              <label for="preco"><b>Preço</b></label>
              <input
                type="number"
                placeholder="Introduza um preço"
                name="preco"
                v-model="form.preco"
                required
              />
  
              <button type="submit">Registrar Equipamento</button>
            </div>
          </form>
        </div>
      </section>
  
      <footer>
        <p>&copy; 2025 Loja Tech - Todos os direitos reservados.</p>
      </footer>
    </div>
  </template>
  
  <script>
export default {
  name: 'CriarConta',
  data() {
    return {
      form: {
        marca: '',
        modelo: '',
        tipo: '',
        categoria: '',
        preco: '',
      },
    };
  },
  methods: {
    async submitForm() {
      try {
        const response = await fetch('http://localhost:3001/api/equipamentos', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(this.form),
        });

        if (!response.ok) throw new Error('Erro ao registrar equipamento');

        const data = await response.json();
        console.log('Equipamento salvo no MongoDB:', data);

        alert('Equipamento registrado com sucesso!');

        // Limpa o formulário
        this.form = {
          marca: '',
          modelo: '',
          tipo: '',
          categoria: '',
          preco: '',
        };
      } catch (error) {
        console.error('Erro:', error);
        alert('Erro ao registrar equipamento.');
      }
    },
  },
};
</script>

  
  <style scoped>
  header {
    background: #0d6efd;
    color: #fff;
    padding: 20px;
    text-align: center;
  }
  
  .imgcontainer {
    text-align: center;
    margin: 20px;
  }
  
  .container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 20px;
    max-width: 400px;
    margin: auto;
  }
  
  button {
    background: #0d6efd;
    color: white;
    padding: 10px;
    border: none;
    cursor: pointer;
    font-weight: bold;
  }
  
  button:hover {
    background: #084298;
  }
  body {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f8f9fa;
    color: #333;
}
header {
    background: #0d6efd;
    color: #fff;
    padding: 20px;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}
nav ul {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: center;
    gap: 20px;
}
nav ul li {
    display: inline;
}
nav ul li a {
    color: #fff;
    text-decoration: none;
    font-weight: bold;
}
.banner {
    text-align: center;
    padding: 60px 20px;
    background: linear-gradient(to right, #0d6efd, #6610f2);
    color: #fff;
}
.produtos {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
    padding: 40px;
    max-width: 1200px;
    margin: 0 auto;
    
}


footer {
    background: #0d6efd;
    color: #fff;
    text-align: center;
    padding: 20px;
    margin-top: 40px;
    font-size: 14px;
}

form {
    border: 3px solid #f1f1f1;
  }
  
  /* Full-width inputs */
  input[type=text], input[type=password] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    box-sizing: border-box;
  }
  
  /* Set a style for all buttons */
  button {
    background-color: #04AA6D;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
  }
  
  /* Add a hover effect for buttons */
  button:hover {
    opacity: 0.8;
  }
  
  /* Extra style for the cancel button (red) */
  .cancelbtn {
    width: auto;
    padding: 10px 18px;
    background-color: #f44336;
  }
  
  /* Center the avatar image inside this container */
  .imgcontainer {
    text-align: center;
    margin: 24px 0 12px 0;
  }
  
  /* Avatar image */
  img.avatar {
    width: 40%;
    border-radius: 50%;
  }
  
  /* Add padding to containers */
  .container {
    padding: 16px;
  }
  
  /* The "Forgot password" text */
  span.psw {
    float: right;
    padding-top: 16px;
  }
  
  /* Change styles for span and cancel button on extra small screens */
  @media screen and (max-width: 300px) {
    span.psw {
      display: block;
      float: none;
    }
    .cancelbtn {
      width: 100%;
    }
  }
  </style>
  